import "core-js/modules/es.array.push.js";
import AdminToggleDark from './AdminToggleDark.vue';
export default {
  name: 'DashboardTabs',
  props: {
    activeTab: {
      type: String,
      default: 'dashboard'
    }
  },
  components: {
    AdminToggleDark
  },
  computed: {
    titleName() {
      const titles = {
        dashboard: '文件管理',
        customerConfig: '用户管理',
        systemConfig: '系统设置',
        upload: '文件上传'
      };
      return titles[this.activeTab] || '文件管理';
    },
    iconName() {
      const icons = {
        dashboard: 'images',
        customerConfig: 'user-cog',
        systemConfig: 'cogs',
        upload: 'upload'
      };
      return icons[this.activeTab] || 'images';
    }
  },
  methods: {
    refreshDashboard() {
      location.reload();
    },
    handleTabClick(tab) {
      this.$router.push(`/${tab}`);
    },
    handleDropdownVisible(isVisible) {
      const trigger = document.querySelector('.dropdown-trigger');
      if (trigger) {
        if (isVisible) {
          trigger.classList.add('is-open');
        } else {
          trigger.classList.remove('is-open');
        }
      }
    }
  }
};